.routes {
  padding: 24px 16px;
  background-color: #131414;
  max-height: calc(100vh - 77px);
  overflow: auto;
}

.routes::-webkit-scrollbar {
  width: 5px;
  background: #414242;
  border-radius: 20px;
}
.routes::-webkit-scrollbar-thumb {
  background: #1cc8ee;
  border-radius: 20px;
}

@media (min-width: 768px) {
  .routes {
    max-height: calc(100vh - 92px);
  }
}

.routes_city {
  color: #f5f5f5;
  font-size: 16px;
  line-height: 23px;
}

.routes_list {
  margin-top: 6px;
}

.routes_list .route {
  display: block;
  padding: 12px 16px;
  color: #f5f5f5;
  font-size: 14px;
  line-height: 20px;
  border-radius: 6px;
}

.routes_list .route:nth-child(odd) {
  background-color: #1c1d1d;
}

.routes_list .route_name {
  display: block;
  color: #1cc8ee;
  font-weight: 500;
  font-size: 17px;
  line-height: 25px;
}

.route_fromTo {
  margin: 0;
}

.route_fromTo span {
  color: #1cc8ee;
}

.routes .no_routes {
  font-size: 17px;
  color: #f5f5f5;
  display: block;
  text-align: center;
}
